import React, { Component, useState } from 'react'
import { Button, Modal, ModalBody, ModalFooter, ModalHeader,ModalTitle} from 'reactstrap';
import './bootstrap/css/bootstrap.css'
import './Card.css'
import { withNamespaces } from 'react-i18next';
import {ReactComponent as ForkSvg} from  './gitfork.svg'
import ReactMarkdown from 'react-markdown';
const gfm = require('remark-gfm')


class Card extends Component {
    constructor(props){
        super(props)
        const {t} = this.props
        this.state={ 
            showModal: false,
            title: props.title,
            description: props.description,
            forked: props.forked,
            showDirection: props.direction,
            readme_raw: null,
            getRepo: props.getRepo || true,
            modal:props.modal
        }
        
        
    }
    toggleModal(){
       this.setState({showModal: !this.state.showModal})
    }
    
    //Using Javascript's atob to decode base64 doesn't properly decode utf-8 strings IF ONLY I KNEW THIS EARLIER
    //https://stackoverflow.com/questions/30106476/using-javascripts-atob-to-decode-base64-doesnt-properly-decode-utf-8-strings
    b64DecodeUnicode = str => {
        // Going backwards: from bytestream, to percent-encoding, to original string.
        return decodeURIComponent(atob(str).split('').map(function(c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
        }).join(''));
    }
    componentDidMount(){
        if (this.props.getRepo){
        fetch("https://api.github.com/repos/Likecinema/"+this.state.title+"/contents/README.md")
        .then(res => res.json()
        .then(data =>{if (data["content"])this.setState({readme_raw: this.b64DecodeUnicode(data["content"])});else this.setState({readme_raw:"No README.md found."})}
        ,(error => this.setState({readme_raw:"Error when fetching README.md. More details:" + error}))))
        }
    }
    render() {
        const FitImage = props => {return <img src={props.src} alt={props.alt} className="img-fluid">{props.children}</img>}
        const renderers = {
            image: FitImage,
        }
        const {title, description, showDirection, forked} = this.state;
        const {t} = this.props
        return (
            <div className="card">
                <div className="wrapper">
                    <a onClick={()=>this.toggleModal()}>
                        <h2>{title? title : "No Title Given"}</h2>
                        {description ? description : "No Description"}
                    </a>
                    {this.state.modal &&
                    <Modal isOpen={this.state.showModal} centered={true} toggle={() => this.toggleModal()} backdrop={true}>
                    <ModalHeader>
                    {title}
                    </ModalHeader>
                    <ModalBody>
                        {this.state.readme_raw? <ReactMarkdown plugins={[gfm]} renderers={renderers}>{this.state.readme_raw}</ReactMarkdown>: this.state.description}
                    </ModalBody>
                    <ModalFooter><a onClick={()=> this.toggleModal()}>{t('Close')}</a></ModalFooter>
                    </Modal>}
                    {forked && <div className="forked"><ForkSvg alt="This repo was forked" fill="white"/></div>}

                </div>
            </div>
        )
    }
}

export default withNamespaces()(Card)
