//Background source: https://codepen.io/stealy/pen/zYxXpza

import React, {useState} from 'react';
import './Card.js'
import i18n from 'i18next';
import { withNamespaces } from 'react-i18next';
import './Background.css'
import './Card.css'
import {ReactComponent as CloudSvg} from  './cloud.svg'
import {ReactComponent as StarSvg} from  './star.svg'
// Replace SVG file with your logo
import {
  NavLink,
  HashRouter,
  Link,
  Route,
  Switch,
  BrowserRouter
} from "react-router-dom";
import './App.css';
import Projects from './Projects';
import CV from './CV';
import Main from './Main'
var lang = true;
function App({t}) {
  var [clouds, setClouds] = useState(true)
  var [stars, setStars] = useState(true)
  var [cloudStyle, setCloudStyle] = useState(true)
  var [starStyle, setStarStyle] = useState(true)

  const changeLanguage = (lng) => {
    lang = !lang;
    lng ? i18n.changeLanguage('en'): i18n.changeLanguage('el');
  }
  const whiteFill= {
    "fill":"white"
  }
  const whiteStroke = {
    "fill": "None",
    "stroke": "white",
    "strokeWidth": 2
  }
    return (
      <BrowserRouter>
      <div className="App">
      <div className="navigation">
      <div className="App-logo">
      <NavLink to="/" className = "MyName">{t('MyName')}<p>{t('UndergraduateAtCeid')}</p></NavLink></div>
        <div className="navigation-sub">
        <NavLink to="/" className="item show-left">{t('Home')}</NavLink>
          <NavLink to="/projects" className="item show-down" >{t('Projects')}</NavLink>
          <NavLink to="/cv" className="item show-in">CV</NavLink>

        </div>
        <hr className="ruler show-in"></hr>
      </div>
      <div className="content">
        <Route exact path="/" component={Main}></Route>
        <Route path="/projects" component={Projects}></Route>
        <Route path="/cv" component={CV}></Route>
      </div>
      <div className="background">
      <div className="stars"></div>
      {stars && <div className="twinkling"></div>}
      {clouds && <div className="clouds"></div>}
      </div>
      <div className="footer">
        <hr className = "ruler show-in"></hr>
          <div className="footerGrid">
            <div><a onClick={() =>{changeLanguage(!lang)}}>{t('SwitchToLanguage')}</a></div>
            <CloudSvg style={cloudStyle? whiteFill : whiteStroke} alt="Toggle Clouds" onClick={() => {setClouds(!clouds);setCloudStyle(!cloudStyle)}} className="footerSvg"/>
            <StarSvg style={starStyle? whiteFill : whiteStroke} alt="Toggle Stars" onClick={() => {setStars(!stars);setStarStyle(!starStyle)}} className="footerSvg"/>
            <div><a target="_blank" href="https://codepen.io/stealy/pen/zYxXpza">{t('BackgroundSource')}</a></div>
            </div>
    </div>
    </div>
    </BrowserRouter>
  );
}
 
export default withNamespaces()(App);