import React from 'react'
import './CVinfo.css'
import { withNamespaces } from 'react-i18next';
function CVinfo(props) {
        var state = props["children"]
        return (
        <div className="CVinfo">{
            state.map(item => <div key={Object.keys(item)[0]}><p></p><h4>{Object.keys(item)[0]}</h4><h6>{item[Object.keys(item)]}</h6></div>)}
            </div>)
}

export default withNamespaces()(CVinfo)
