import React, {Component} from 'react'
import Card from './Card.js'
import CVinfo from './CVinfo'
import './CVinfo.css'
import './i18n'
import { withNamespaces } from 'react-i18next';
import {Link} from 'react-router-dom'



class CV extends Component{
    constructor(props){
        super(props)
        // const {t} = this.props;
        this.state = {
        }
        
        this.t = this.props.t
        
    }

        generateJson = (t) => {
            const CVJson = {"CV":[{
                "name": this.t('PersonalInfo'),
                "description": [{
                        [this.t("NameTag")]: this.t("FullName")
                    },
                    {
                        [this.t('Phone')]: "(0030) 6976815352"
                    },
                    {
                        "Email": "mauksalah@yahoo.gr"
                    },
                    {
                        [this.t("DateOfBirth")]: "20/01/1998"
                    },
                    {
                        [this.t("CurrentLocation")]: this.t("Town")
                    }
                ]
            }, {
                "name": this.t("Education"),
                "description": [{
                        "2016": this.t("Enrolled")},
                    {
                        "2014-2016": this.t("Graduated")
                    }
                ]
            }, {
                "name": this.t("Skills"),
                "description": [{
                        [this.t("ProgrammingLanguages")]: this.t("ProgrammingLanguagesText")
                    },
                    {
                        [this.t("OperatingSystems")]: this.t("OSText")
                    },
                    {
                        "Pentesting": this.t("PentestText")
                    }
                ]
            }, {
                "name": this.t("WorkExperience"),
                "description": [{
                    "Web Development": this.t("AgrodrasiText")
                }]
            }]}
            return CVJson;
        }
    componentDidUpdate(){
        this.CVJson = this.generateJson(this.t)
        this.render()
    }

    render(){
        this.CVJson = this.generateJson(this.t)
        return(
        <div className="fadein">
        {this.CVJson.CV.map((item) => 
            <Card modal={true} getRepo={false} key={item.name} title={item.name}  description={<CVinfo>{item.description}</CVinfo>} forked={item.fork} className="fadein">     
            </Card>)}
            {this.t('pdfCV')}
                <Link target="_blank" to="/resumes/Akis-Sal-CV-Eng.pdf" download>{this.t('pdfCVEng')}</Link>
                <Link target="_blank" to="/resumes/Akis-Sal-CV-El.pdf" download>{this.t('pdfCVEl')}</Link>
                <br/>
            </div>
        )
    }

}

export default withNamespaces()(CV)