import React, {Component} from 'react'
import Card from './Card.js'
import './Projects.css'
import './i18n'
import { withNamespaces } from 'react-i18next';


class Projects extends Component{
    constructor(props){
        super(props);
        const {t} = this.props;
        this.state = {
            error: null,
            isLoaded: false,
            items:[]
        }

    }
    componentDidMount(){
        fetch("https://api.github.com/users/Likecinema/repos")
        .then(res => res.json())
        .then(
            (result) => {
            try{
            this.setState({
                isLoaded: true,
                left: result.filter((v, i) => i % 2),
                right: result.filter((v, i) => !(i % 2)),
                items: result
            })}
            catch{
                this.setState({
                    isLoaded: true,
                    left: [{name: "Timed Out (1)", description: "Your IP reached the maximum amount of GET requests on GitHub API"},{name: "Timed Out (3)", description: "I'm sorry about that, no backend means relying on GitHub to provide info"}],
                    right: [{name: "Timed Out (2)", description: "Please refresh again in an hour or so"},{name: "Timed Out (4)", description: "Thank you for your patience. While you wait, you can visit my actual GitHub for more info"}]
                })
            }
        }, (error) =>{
                this.setState({
                    isLoaded: true,
                    error
                })
            }
        )

    }
    render(){
        const {t} = this.props;
        const {error, isLoaded, items, left, right} = this.state;
        if (error){
            return <div> Error: {error.Message}</div>
        }
        else if (!isLoaded){
            return <div>Loading...</div>

        }
        else {
            return (
                <div><p>{t('ProjectNote')}</p>
                <div className="hrgrid">
                <div className="left">
                  {left.map((item) => <Card modal={true} getRepo={true} className="card" key={item.name} title={item.name}  description={item.description} forked={item.fork} className="left"></Card>)}
                </div><div className="right">
                  {right.map((item) => <Card modal={true} getRepo={true} className="card" key={item.name} title={item.name}  description={item.description} forked={item.fork} className="right"></Card>)}
                  </div>
                </div>
                </div>
              );
            }
      }
}

export default withNamespaces()(Projects)