import React, {Component} from 'react'
import './App.css'
import './Main.css'
import {ReactComponent as GitHubSvg} from  './github.svg'
import {ReactComponent as FacebookSvg} from  './facebook.svg'
import {ReactComponent as InstagramSvg} from  './instagram.svg'
import {ReactComponent as EmailSvg} from  './email.svg'
import { withNamespaces } from 'react-i18next';

class Main extends Component{
    render(){
        const {t} = this.props 
        return(
            <div className="MainWrapper">
            <div className="topside show-in"> 
                {t('AboutMe')}
                <p>
                </p>   
                    {t('LongTermGoals')}

                    <ul>
                        <li>{t('GoalDiploma')}</li>
                        <li>{t('GoalLearn')}</li>
                        <li>{t('GoalSocialNetwork')}</li>
                    </ul>
                <p>
                {t('ReactTest')}
                </p>
           </div>
           <hr className="ruler show-in"></hr>
           <div className="botside show-in">
                {t('ContactMe')}
                <p><a target="blank" href="https://www.github.com/Likecinema"><GitHubSvg className="icon"/></a><a target="blank" href="https://www.facebook.com/akisskate"><FacebookSvg className="icon"/></a><a target="blank" href="https://www.instagram.com/akis_sal"><InstagramSvg className="icon"/></a><a target="blank" href="mailto://mauksalah@yahoo.gr"><EmailSvg className="icon"/></a></p>
           </div>
           </div>
        );
    }
}

export default withNamespaces()(Main)
